<template>
  <div class="hero is-fullheight is-warning is-light">
    <div class="hero-body">
      <div class="container">
        <h1 class="title has-text-centered">
          <img src="../assets/logo.png" width="140" alt="Brasil Serviços: O portal de oportunidades!">
        </h1>
        <div class="columns is-vcentered">
          <div class="column is-4 is-offset-4">
            <form @submit.stop.prevent="login">
              <div class="field">
                <p class="control has-icons-left">
                  <input type="email" class="input" v-model="username" placeholder="E-mail" :disabled="loading">
                  <span class="icon is-small is-left">
                    <i class="fa fa-envelope"></i>
                  </span>
                </p>
              </div>
              <b-field>
                <b-input type="password" placeholder="Senha" v-model="password" password-reveal icon="lock" :disabled="loading"></b-input>
              </b-field>
              <div class="field">
                <p class="control">
                  <button type="submit" :class="{ 'button': true, 'is-loading': loading, 'is-fullwidth': true, 'is-primary': true }">Entrar</button>
                </p>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      username: '',
      password: '',
      loading: false
    }
  },
  methods: {
    login () {
      console.log('logged!')
    }
  }
}
</script>


<style>
.hero-head .navbar {
  background: #7957d5;
  color: #fff;
  box-shadow: 0 1px 0 hsla(0, 0%, 100%, .2);
}
</style>



